<template>
  <div class="order">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row>
        <el-col :span="12">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            class="input-with-select"
            clearable
            @input="queryChange"
            @clear="queryClose"
          >
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="orderList" border>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number" width="200"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === '0'" type="danger"></el-tag>
            <el-tag v-if="scope.row.pay_status === '1'" type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" prop="create_time" width="140">
          <template slot-scope="scope">{{scope.row.create_time | timeFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              :enterable="false"
              content=""
              placement="top"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editOrder(scope.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-button
              type="success"
              icon="el-icon-location-outline"
              size="mini"
              @click="checkLocation(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="queryInfo.pagesize"
        :page-sizes="[1, 2, 5, 10]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <el-dialog title="" :visible.sync="showEditLocateForm" width="50%">
      <el-form :model="editInfo" :rules="editRules" label-width="90px" ref="addAttrRef">
        <!--rulesrule-->
        <el-form-item label="/" prop="city">
          <el-cascader :options="cityData" v-model="editInfo.detail"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input v-model="editInfo.detail"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showEditLocateForm = false"> </el-button>
        <el-button type="primary" @click="confirmEdit(editInfo)"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="showLocation" width="60%">
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in activities"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import cityData from "plugins/citydata.js";
export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      orderList: [],
      currentPage: 1,
      showEditLocateForm: false,
      editInfo: {
        city: "",
        detail: ""
      },
      editRules: {
        city: [{ required: true, message: "/", trigger: "blur" }],
        detail: [{ required: true, message: "", trigger: "blur" }]
      },
      cityData,
      showLocation: false,
      activities: [],
      timer: null
    };
  },
  created() {
    this.getOrderList();
  },
  methods: {
    getOrderList() {
      this.$http.get("orders", { params: this.queryInfo }).then(res => {
        //console.log(res)
        if (res.data.meta.status !== 200)
          return this.$message.error("");
        this.total = res.data.data.total;
        this.orderList = res.data.data.goods;
      });
    },
    editOrder(order) {
      this.showEditLocateForm = true;
    },
    checkLocation(order) {
      this.$http.get('/kuaidi/1106975712662').then(res => {
        if(res.data.meta.status !== 200)
          return this.$message.error("")
        this.activities = res.data.data
      }).catch(err => err)
      this.showLocation = true;
    },
    queryChange(e){
      console.log(e)
      if(this.timer) clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.queryInfo.query = e
        this.getOrderList()
      }, 1000)
    },
    queryClose(){
      this.queryInfo.query = ''
      //this.getUserList()
    },
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.getOrderList();
    },
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val;
      this.getOrderList();
    }
  }
};
</script>
<style lang="scss" scoped>
.el-cascader {
  width: 100%;
}
</style>
